import React, { useEffect } from 'react'
import { useState } from 'react'
import { useColourObjectStore } from '../../../store/ColourStore'
import * as back from '../../../assets/Clean Walk/Video/VID_next.png'
import * as tick from '../../../assets/tick.svg'
import './style.css'
import Animationcomponent from '../Animation';
import { useActiveScreen } from '../../../store/ActiveScreen';
import { useActivePopup } from '../../../store/ActivePopup';
import { levelstore } from '../../../store/levelStore';
import Loader from '../Loader'

const Popup = (props: any) => {

  const { classname, title, desc1, desc2, popup_body, confirmClick, cancelClick, actionable, back_btn, tick_btn,spinner } = props;

  const { level } = levelstore();
  const { colourObj } = useColourObjectStore();
  const [backendresponse_popup, setbackendresponse_popup] = useState(false);
  const [backendresponse, setbackendresponse] = useState('');
  const [popup_Title_Text, setpopup_Title_Text] = useState(true);
  const [popup_description_Text, setPopup_description_Text] = useState(true);
  const [popus_body_container, setPopus_body_container] = useState(true);
  const [popout, setpopout] = useState(true);

  useEffect(() => {
    if ((popup_Title_Text === null) || (popup_Title_Text === undefined)) {
      setpopup_Title_Text(false)
    }
    if (
      (desc1 === null || desc1 === undefined) &&
      (desc2 === null || desc2 === undefined)
    ) {
      setPopup_description_Text(false)
    }
    if (popus_body_container === null || popus_body_container === undefined) {
      setPopus_body_container(false)
    }
  }, [])

  // const fade = (props) => {
  //   let result = (popout ? (props + " " + "fade_in") : (props + " " + "fade_out"))
  //   return result
  // }

  // const fadezoom = (props) => {
  //   let result = (popout ? (props + " " + "fadezoom_in") : (props + " " + "fadezoom_out"))
  //   return result
  // }

  // const ani = (props) => {
  //   let result = ""
  //   if (p2 === "f") {
  //     result = (popout ? (p1 + " " + "fade_in") : (p1 + " " + "fade_out"))
  //   }
  //   else if (p2 === "fz") {
  //     result = (popout ? (p1 + " " + "fadezoom_in") : (p1 + " " + "fadezoom_out"))
  //   }
  //   return result
  // }

  return (
    <div
      className={
        !classname ? 'popup_wrapper' : 'popup_wrapper' + ' ' + classname
      }
    >
      <Animationcomponent
        className={'popup_bg'}
        Animationtype={'fade_in'}
        onClick={() => {
          setpopout(false)
          setTimeout(() => {
            cancelClick()
          }, 290);
        }}>

      </Animationcomponent>

      <div className="popup_container">
        <Animationcomponent
          className={'popup glow'}
          style={{ backgroundColor: 'white', borderColor: colourObj.color_m3 }}
          Animationtype={'fadezoom_in'}
        >
          {backendresponse_popup ? (
            <>
              <div className='title'>{backendresponse}</div>
              {/* <div className='popup_description'>
                  <div className='popup_text' style={{ color: colourObj.color_m1 }}>The following Bracket Entry will be updated!</div>
                  </div> */}
              <div className='popup_button_container'>
                <div
                  onClick={() => {
                    console.log('***CANCEL***')
                    setbackendresponse_popup(false)
                  }}
                  className='popup_cancel_button'
                >
                  OK
                </div>
              </div>
            </>
          ) : (
            <>
              {popup_Title_Text ? (
                <div
                  className='popup_title'
                  style={{
                    color: colourObj.color_m1,
                    textDecorationColor: colourObj.color_m3
                  }}
                >
                  {title}
                </div>
              ) : null}

              {popup_description_Text ? (
                <div className='popup_description'>
                  <div
                    className='popup_text'
                    style={{ color: colourObj.color_m1 }}
                  >
                    {desc1}
                  </div>
                  <div
                    className='popup_text'
                    style={{ color: colourObj.color_m1 }}
                  >
                    {desc2}
                  </div>
                </div>
              ) : null}

              {popus_body_container ? popup_body : null}

              {actionable !== false && (
                <div className='popup_button_container'>
                  <div
                    onClick={() => {
                      setpopout(false)
                      setTimeout(() => {
                        cancelClick()
                      }, 290)
                    }}
                    className='popup_cancel_button'
                  >
                    Cancel
                  </div>
                  <div
                    onClick={confirmClick}
                    className='popup_submit_button'
                    style={{
                      color: colourObj.color_m2,
                      borderColor: colourObj.color_m2
                    }}
                  >
                    Confirm
                  </div>
                </div>
              )}
            </>
          )}
        </Animationcomponent>

        {back_btn === true && (
          <Animationcomponent
            className={'back glow'}
            style={{ borderColor: colourObj.color_m3 }}
            onClick={() => {
              cancelClick()
            }}
            Animationtype={'fade_in'}
          >
            <img className='icon rotateimg180' src={back} />
          </Animationcomponent>
        )}

        {tick_btn === true && (
          <Animationcomponent
            className={'back glow'}
            style={{ borderColor: colourObj.color_m1 }}
            Animationtype={'fade_in'}
            onClick={() => {
              setTimeout(() => {
                cancelClick()
              }, 500)
            }}
          >
            <img className='submit-tick-icon' src={tick} />
          </Animationcomponent>
        )}

        {spinner === true && (
          <Animationcomponent
            className={'back glow'}
            style={{ borderColor: colourObj.color_m1 }}
            Animationtype={'fade_in'}
            onClick={() => {
              setTimeout(() => {
                cancelClick()
              }, 500)
            }}
          >
            <Loader/>
          </Animationcomponent>
        )}
      </div>
    </div>
  )
}

export default Popup
